<template>
    <ion-page>
        <ion-header>
            <ion-toolbar>
                <ion-buttons slot="start">
                    <ion-back-button default-href="/home"></ion-back-button>
                </ion-buttons>
                <ion-title>簡訊號碼驗證</ion-title>
            </ion-toolbar>
        </ion-header>
        <ion-content class="verify-register-form">
            <ion-grid>
                <ion-row>
                    <ion-col size="12">
                        <ion-item class="input">
                            <ion-label>　簡訊驗證碼　</ion-label>
                            <ion-input type="tel" v-model.trim="code"></ion-input>
                        </ion-item>
                    </ion-col>
                    <ion-col size="6">
                        <ion-button class="button-2" @click="retry">重新發送驗證碼</ion-button>
                    </ion-col>
                    <ion-col size="6">
                        <ion-button class="button-1" @click="submit">進行驗證</ion-button>
                    </ion-col>
                    <ion-col size="12">
                        <ion-text>* 重新發送簡訊驗證碼需間隔 1 分鐘。</ion-text>
                    </ion-col>
                </ion-row>
            </ion-grid>
        </ion-content>
    </ion-page>
</template>

<script>
    import {
        registerSms,
        verifyRegister
    } from "../api";

    import {
        IonBackButton,
        IonButton,
        IonButtons,
        IonCol,
        IonContent,
        IonGrid,
        IonHeader,
        IonInput,
        IonItem,
        IonLabel,
        IonPage,
        IonRow,
        IonText,
        IonTitle,
        IonToolbar
    } from "@ionic/vue";

    export default {
        components: {
            IonBackButton,
            IonButton,
            IonButtons,
            IonCol,
            IonContent,
            IonGrid,
            IonHeader,
            IonInput,
            IonItem,
            IonLabel,
            IonPage,
            IonRow,
            IonText,
            IonTitle,
            IonToolbar
        },
        data() {
            return {
                code: "",
                token: this.$route.params.token
            };
        },
        methods: {
            retry() {
                registerSms({
                    token: this.token
                }, () => {
                    alert("驗證碼重發成功");
                });
            },
            submit() {
                verifyRegister({
                    code: this.code,
                    token: this.token
                }, () => {
                    alert("帳號註冊成功");
                    this.$router.push({name: "Home"});
                });
            }
        }
    };
</script>

<style scoped>
    ion-grid {
        margin: 20px 20px 0 20px;
    }
    ion-text {
        color: #000;
    }
</style>
